<template>
  <div class="modal animated fadeIn fast">
    <div class="modal-container" style="margin-top: 100px;">
      <button class="modal-close-btn" @click="() => store.isShowWithdrawModal = false" />
      <div class="modal-title">
        Withdraw from stake
      </div>
      <div v-if="!msg">
        <div class="gray">Your stake:</div>
        <div class="fs30 mt-15">{{stake + (isRequestQuit ? revenue : 0)}}<span class="fs15">&nbsp;TOMO</span></div>
        <div v-if="isInPool && !isRequestQuit && !isLoading" class="gray">Revenue:</div>
        <div v-if="isInPool && !isRequestQuit && !isLoading" class="fs40 mt-15 green">{{revenue}}<span class="fs25">&nbsp;TOMO</span></div>
        <button v-if="isInPool && !isRequestQuit && !isLoading" class="btn primary mt10" @click="withdraw">Withdraw Your Revenue</button>
        <button v-if="!isInPool && !isLoading" class="btn mt10 primary" @click="quitPool">
          Withdraw to balance
        </button>
        <button v-if="isInPool && !isRequestQuit && !isLoading" class="btn mt10 secondary" @click="quitPool">
          Request Quit Pool
        </button>
        <button v-if="isInPool && isRequestQuit && !isLoading" class="btn mt10 primary" @click="quitPool">
          {{quitText}}
        </button>
      </div>
      <div v-else>
        <span :style="{color: !isError ? 'green' : 'red'}">{{msg}}</span>
      </div>
    </div>
  </div>
</template>

<script>
import QRCode from '@xkeshi/vue-qrcode';
import _store from '../../store';
import Contract from '../../contracts';
import utils from '../../utils';

export default {
  components: {
    QRCode
  },
  data() {
    return {
      isLoading: true,
      store: _store,
      amount: 1,
      stake: 0,
      isInPool: false,
      revenue: 0,
      quitAtBlock: 0,
      msg: '',
      isError: false,
      currentBlock: 0,
      transactionData: `tomochain:${Contract.get.contractAddress()}?amount=${1}&message=Join%20Stake%20MaxBet`
    }
  },
  watch: {
    'amount': {
      handler(val) {
        this.transactionData = `tomochain:${Contract.get.contractAddress()}?amount=${val || 1}&message=Join%20Stake%20MaxBet`;
      }
    }
  },
  async created() {
    Contract.get.stake(this.store.address)
    .then(stake => {
      this.quitAtBlock = parseInt(stake.quitAtBlock);
      this.stake = utils.toTOMO(stake.amount);
      this.revenue = utils.toTOMO(stake.profit);
      this.isInPool = stake.isInPool;
      this.isLoading = false;
    });
    this.currentBlock = (await Contract.get.lastBlock(true) || {}).number;
    Contract.get.onListenEvent('NEW_BLOCK', e => this.updateCurrentBlock(e), 'WITHDRAW_STAKE_POOL_NEW_BLOCK');
  },
  destroyed() {
    Contract.get.offListenEvent('NEW_BLOCK', 'WITHDRAW_STAKE_POOL_NEW_BLOCK');
    clearInterval(this.timmerUpdateCurrentBlock);
  },
  computed: {
    isRequestQuit() {
      return this.quitAtBlock > 0;
    },
    quitText() {
      if (this.currentBlock > this.quitAtBlock) {
        return 'Withdraw to balance';
      } else {
        if (this.currentBlock == 0) {
          return 'Can Withdraw after ...';
        }
        else {
          return `Can Withdraw after ${(this.quitAtBlock > this.currentBlock ? this.quitAtBlock - this.currentBlock : 0)*2}s`;
        }
      }
    }
  },
  methods: {
    updateCurrentBlock(lastBlock) {
      this.currentBlock = parseInt(lastBlock.number);
    },
    async withdraw() {
      if (this.isSubmitting) return;
      this.isSubmitting = true;
      try {
        var hash = await Contract.withdrawProfit();
        this.isSubmitting = false;
        this.msg = 'Your transaction is in processing.';
        var tx = await Contract.get.checkTx(hash);
        this.msg = 'Withdraw successfully';
        this.store.updateBalance();
      }
      catch(ex) {
        if (ex.toString().toLowerCase().indexOf('user denied transaction signature') >= 0) {
          return;
        }
        console.error(ex);
        this.msg = 'Error, Cannot withdraw. Please try again.';
        this.isError = true;
      }
    },
    async quitPool() {
      if (this.isSubmitting) return;
      this.isSubmitting = true;
      try {
        if (this.quitAtBlock && this.quitAtBlock > this.currentBlock) {
          return;
        }
        var hash = await Contract.quitPool();
        this.isSubmitting = false;
        this.msg = this.quitAtBlock > 0 || !this.isInPool ?
          `Your transaction is in processing` :
          'Your request is in processing';
        var tx = await Contract.get.checkTx(hash);
        this.msg = this.quitAtBlock > 0 || !this.isInPool ?
          `Withdrawn ${this.stake} TOMO to your balance` :
          'Request Submited. Withdrawal available after 50 blocks (about 100 seconds)';
        this.store.updateBalance();
      }
      catch(ex) {
        if (ex.toString().toLowerCase().indexOf('user denied transaction signature') >= 0) {
          return;
        }
        console.error(ex);
        this.msg = 'Error, Cannot quit qool. Please try again.';
        this.isError = true;
      }
    }
  }
}
</script>

<style scoped>
.withdraw-qr {
  width: 130px;
  height: 130px;
  margin: auto;
}

.withdraw-qr canvas {
    height: 130px;
    width: 130px;
}

.withdraw-qr-description {
  font-family: monospace;
  line-height: 1;
  margin-top: 10px;
  font-size: 15px;
  color: gray;
  text-align: center;
}
</style>



